.header {
  background: url("../img/header_bg.jpg") no-repeat center center / cover fixed;
  height: 100vh;
  letter-spacing: 2px;
  padding: 0;

  .row:first-child {
    display: flex;
    align-items: center;
    height: 120px;
  }
}

.logo {
  font-size: 17px;
  text-transform: uppercase;
  color: #fff;
  white-space: nowrap;

  &__text {
    font-weight: 900;

    &_thin {
      font-weight: 100;
    }
  }
}

.menu {
  &__list {
    display: flex;
    justify-content: space-around;
  }

  &__item {
    display: block;
  }

  &__link {
    color: #fff;
    transition: .2s ease;
    position: relative;
    font-weight: 900;

    &::after {
      display: block;
      content: '';
      height: 2px;
      width: 0;
      background: #fff;
      transition: .2s ease;
      position: absolute;
      bottom: -1px;
    }

    &:hover {
      color: darken(#fff, 20%);

      &::after {
        width: 100%;
        background: darken(#fff, 20%);;

      }
    }
  }
}

.mail {
  text-align: right;

  &__link {
    color: #fff;
    border-bottom: 1px solid #fff;
    font-size: 13px;
    font-weight: 300;
    transition: .2s ease;

    &:hover {
      font-size: 15px;
      color: #fff;
      text-shadow: 2px 2px 2px #fff;
    }
  }
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 120px - 110px);

  &__title {
    color: #fff;
    font-size: 49px;
    margin-bottom: 5px;
    white-space: nowrap;
  }

  &__title-text {
    color: #ffffff;
    font-size: 29px;
    font-weight: 100;
    text-transform: uppercase;
  }
}

.next-link {
  display: block;
  position: relative;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid #fff;
  bottom: 0;
  margin: 0 auto;
  transition: .2s ease;
  cursor: pointer;

  i {
    font-size: 16px;
    margin: -8px;
    top: 51%;
    left: 52%;
    color: #fff;
    position: absolute;
    transition: .2s ease;
  }

  &:hover {
    border-color: darken(#fff, 20%);
    background: rgba(#fff, .1);

    i {
      color: darken(#fff, 20%);
    }
  }
}

.bg__text {
  margin-bottom: 32px;

  &:last-child {
    margin-bottom: 0;
  }
}

.merits {
  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    display: block;
    width: 138px;
  }
}

.square-wrapper {
  width: 73px;
  height: 73px;
  padding: 7px;
  transform: rotate(45deg);
  margin-bottom: 26px;
  transition: .2s ease;
}

.square-inner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .2s ease;
}

.merit-int {
  transform: rotate(-45deg);
}

.decor-wrapper {
  border: 1px solid lighten($green, 15%);
}

.decor-inner {
  border: 1px solid lighten($green, 50%);
}

.decor-int {
  color: lighten($green, 15%);
  font-size: 20px;
  font-weight: 100;
  transition: .2s ease;
}

.merit {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  transition: .2s ease;

  &s__text {
    transition: .2s ease;
  }

  &:hover {
    transform: scale(1.2);

    .merits__text {
      transform: scale(1.2);
    }

    .square-wrapper {
      transform: rotate(180deg);
    }

    .merit-int {
      transform: rotate(-180deg);
    }

    .square-inner {
      background: #fff;
    }
  }
}

.contact-wrap {
  padding-top: 52px;
}

.contact {
  display: flex;
  justify-content: space-between;

  &__item {
    display: flex;
    align-items: center;
    height: 30px;
  }

  &__link {
    border-bottom: 1px solid lighten($green, 5%);
    color: $green;
    text-transform: lowercase;
    font-weight: 300;
    font-size: 14px;
    transition: .2s ease;
    margin: 2px 0;
    display: inline-block;

    &:hover {
      transform: scale(1.2);
      color: lighten($green, 10%);
      border-bottom: 1px solid lighten($green, 15%);
    }
  }
}

.write-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.me {
  display: flex;
  width: 64px;
  height: 64px;
  justify-content: center;
  align-items: center;
  background: darken(#fff, 15%);
  border-radius: 50%;
  font-size: 30px;
  color: $green;
  transition: .2s ease;
  margin-right: 20px;

  &:hover {
    color: lighten($green, 10%);
    background: darken(#fff, 30%);
  }
}

.write {
  margin-right: 30px;
}

.skills {
  &:before,
  &:after {
    content: '';
    height: 60px;
    width: 100%;
    background: rgba(#000, .2);
    position: absolute;
    left: 0;
    top: 0;
    border-bottom: 2px solid #000;
  }

  &:after {
    left: auto;
    right: 0;
    top: 60px;
    height: calc(100% - 60px);
    width: 60px;
    border-bottom: none;
    border-left: 2px solid #000;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
    width: 240px;
    transition: .2s ease;

    &:hover {
      transform: scale(1.3);

      .skills__round-wrapper {
        background: lighten(#000, 75%);
      }

      .skills__round-inner {
        background: #fff;
      }
    }
  }

  &__round-wrapper {
    width: 61px;
    height: 61px;
    padding: 7px;
    border-radius: 50%;
    transition: .2s ease;
  }

  &__round-inner {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: .2s ease;
  }

  &__round_int {
    font-size: 13px;
  }

  &__name {
    font-weight: 900;
    margin-left: 23px;
  }
}


.work {
  &:before,
  &:after {
    content: '';
    height: 60px;
    width: 100%;
    background: rgba(#000, .2);
    position: absolute;
    left: 0;
    bottom: 0;
    border-top: 2px solid #000;
  }

  &:after {
    left: auto;
    right: 0;
    bottom: 60px;
    height: calc(100% - 60px);
    width: 60px;
    border-top: none;
    border-left: 2px solid #000;
  }

  &__item {
    width: 100%;
    overflow: hidden;
    transition: .2s ease;


    &-wrap {
      overflow: hidden;
    }

    &:hover {
      transform: scale(1.15);
    }
  }

  &__overflow {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background: rgba(#000, .5);
    transition: .2s ease;
    opacity: 0;

    &:hover {
      opacity: 1;
    }
  }

  &__title {
    font-size: 15px;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #ffffff;
  }

  &__link {
    font-size: 13px;
    font-weight: 300;
    color: #ffffff;
    text-decoration: underline;
    padding: 5px;

    &:hover {
      font-size: 16px;
      color: #ffffff;
      text-decoration: underline;
      padding: 2px;
    }
  }
}

@for $i from 1 through 3 {
  .work-pic-#{$i} {
    background: url("../img/pic_#{$i}.jpg") no-repeat center center / cover;
  }
}

.contacts {
  height: calc(100vh - 76px);
  background: #000;
}

.footer {
  display: flex;
  height: 76px;
  padding: 0;
  align-items: center;

  &__text {

  }
}

.selector-wrap,
.links-wrap {
  position: fixed;
  height: 100%;
  width: 60px;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.selector-btn,
.social-link {
  background: none;
  border: none;
  margin: 5px 0;
  outline: none !important;
  cursor: pointer;
  transition: 0.2s ease;
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    transform: scale(1.5);
  }

  &:before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 28px;
    color: lighten($green, 15%);
    transition: .2s ease;
  }
}

.selector-btn:nth-child(1):before {
  content: "\f015";
}

.selector-btn:nth-child(2):before {
  content: "\f2bb";
}

.selector-btn:nth-child(3):before {
  content: "\f6de";
}

.selector-btn:nth-child(4):before {
  content: "\f121";
}

.selector-btn:nth-child(5):before {
  content: "\f0e0";
}

.social-link:before {
  font-family: "Font Awesome 5 Brands";
  color: #000;
}

.social-link:nth-child(1):before {
  content: "\f0e1";
  color: #0077b5;
}

.social-link:nth-child(2):before {
  content: "\f09b";
}

.social-link:nth-child(3):before {
  content: "";
  background: url("../img/freelancehunt.svg") no-repeat center / 100%;
  height: 32px;
  width: 32px;
}

.social-link:nth-child(4):before {
  content: "\f17e";
  background: linear-gradient(to top, #38719f, #00ceff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.social-link:nth-child(5):before {
  content: "\f2c6";
  background: linear-gradient(to top, #3478ad, #04c9ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.social-link:nth-child(6):before {
  content: "\f232";
  background: linear-gradient(to top, #06B824, #62FB7D);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.selector-btn.active {

  transform: scale(1.5);

  &:before {
    color: lighten($green, 30%);
  }
}

.modal {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  opacity: 0;
  color: #000;
  font-size: 18px;
  line-height: 1.75em;
  background: #fff;
  border-radius: 8px;
  border: 5px solid $green;
  width: 80vw;
  height: 80vh;
  margin: 10vh 10vw;
  padding: 40px 30px;
  flex-direction: column;
}

.modal-overlay {
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: pointer;
  z-index: 1;
  display: none;
  opacity: 0;
}

.modal__form {
  display: none;
}

.startModalFooter {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.closeStartModal {
  padding: 12px 0;
  border: 2px solid $green;
  border-radius: 2px;
  background: none;
  width: 25%;
  cursor: pointer;
  transition: .2s ease;
  text-transform: uppercase;
  outline: none !important;

  &:hover {
    background: lighten($green, 80%);
    transform: scale(1.05);
  }
}

.modal-checkbox {
  width: 75%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: row-reverse;
}

.rememberModalWindow {
  margin-left: 10px;
  position: relative;
  height: 31px;
  width: 31px;
  display: block;
  cursor: pointer;
}

.rememberModalWindowLabel {
  position: relative;
  cursor: pointer;
}

.rememberModalWindowLabel:after,
.rememberModalWindow:after {
  content: '';
  width: 0;
  height: 2px;
  position: absolute;
  left: 0;
  bottom: -2px;
  background: $green;
  transition: .2s ease;
}

.rememberModalWindow:after {
  left: auto;
  right: 0;
}

.rememberModalWindow:checked:after {
  width: calc(100% + 10px);
}

.rememberModalWindow:checked ~ .rememberModalWindowLabel:after {
  width: 100%;
}

.links-wrap {
  position: absolute;
  left: 0;
  right: auto;
  background: rgba(#000, .2);
  border-right: 2px solid #000;
}